html {
  font-family: Arial, Helvetica, sans-serif;
  font-size: 20px;
}

body {
  margin: 0;
}

.people {
  grid-template-columns: repeat(2, 220px);
  justify-content: center;
  gap: 40px 30px;
  margin-top: 30px;
  display: grid;
}

@media (width >= 1000px) {
  .people {
    grid-template-columns: repeat(4, 220px);
  }
}

.person {
  background-color: #fff;
  border-radius: 10px;
  padding: 10px 10px 16px;
  transition: transform 1s;
}

.person:hover {
  background-color: #eee;
  transform: scale(1.2);
}

.person:hover .extra {
  visibility: visible;
}

.person__img {
  border-radius: 50%;
  width: 200px;
  height: 200px;
  display: block;
}

.person:hover .person__img {
  border-radius: 60px;
}

.photo {
  position: relative;
}

.rank {
  color: #8b0000;
  background-color: #fff;
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  width: 32px;
  height: 32px;
  display: flex;
  position: absolute;
  top: 50%;
  right: 10px;
  transform: translateY(-50%);
}

.details {
  text-align: center;
  justify-content: center;
  margin-top: 8px;
  display: block;
}

.name {
  color: #2f4f4f;
  text-align: center;
  font-size: 20px;
}

.extra {
  visibility: hidden;
  color: #000;
  margin-top: 9px;
  font-size: 18px;
}

.sport:after {
  content: ",";
}

.pay:before {
  content: "$";
}

.pay:after {
  content: "M";
}
